/**
 * Cursor
 */
const cursor = {}
cursor.x = 0
cursor.y = 0

window.addEventListener('mousemove', (event) =>
{
    cursor.x = event.clientX / sizes.width - 0.5
    cursor.y = event.clientY / sizes.height - 0.5
})

console.log(screenY)






window.addEventListener("click", () => {
    console.log(camera.position, camera.rotation)
})







/**
 * Animate
 */
const clock = new THREE.Clock()
let previousTime = 0

const tick = () =>
{
    const elapsedTime = clock.getElapsedTime()
    const deltaTime = elapsedTime - previousTime
    previousTime = elapsedTime


    
    // Animate camera
    /* if(movec == false){
        camera.position.y = - window.scrollY / sizes.height * objectsDistance
    }
     */
  
    

    const parallaxX = cursor.x * 0.5
    const parallaxY = - cursor.y * 0.5
     let distmax = 0.15


   /*  let rotatey = camera.rotation.y
     camera.rotation.y +=  cursor.x * elapsedTime * -0.1

     let rotate = camera.rotation.y
     camera.rotation.x +=  cursor.y * elapsedTime * -0.1 */







    if(cameraGroup.position.x < distmax  && cameraGroup.position.x > -distmax  ){
        cameraGroup.position.x += (parallaxX) * 5 * deltaTime   
    }
    else{
        if(cameraGroup.position.x < distmax ){
            cameraGroup.position.x += 0.001
        }
        else{
            cameraGroup.position.x -= 0.001
        }
    }

    // Animate meshes
    for(const mesh of sectionMeshes)
    {
        mesh.rotation.x += deltaTime * 0.1
        mesh.rotation.y += deltaTime * 0.12
    }
    

    // Render
    renderer.render(scene, camera)
    /* controls.update(clock.getDelta()) */

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()